import { Component, OnInit, Input } from '@angular/core';

@Component({
  selector: 'app-rating',
  templateUrl: './rating.component.html',
  styleUrls: ['./rating.component.scss'],
})
export class RatingComponent implements OnInit {

  @Input() rate: any;
  stars = [];
  constructor() { }

  ngOnInit() {
    for (let i = 1; i <= 5; i++) {
      this.stars.push(i <= (+this.rate));
    }

  }

}
