import {Component, Input, OnInit} from '@angular/core';
import {ModalController} from '@ionic/angular';
import {MineCopyPage} from './mine-copy.page';

@Component({
    selector: 'app-mine-copy',
    templateUrl: './mine-copy.component.html',
    styleUrls: ['./mine-copy.component.scss']
})
export class MineCopyComponent implements OnInit {
    @Input() tagData: any;

    constructor(public modalCtrl: ModalController) {
    }

    ngOnInit() {
        console.log(this.tagData);
    }

    async openModal() {

        const modal = await this.modalCtrl.create({
            component: MineCopyPage,
            componentProps: {value: this.tagData}
        });
        return await modal.present();
    }


}
