import { Component, OnInit } from '@angular/core';
import { Route, ActivatedRoute } from '@angular/router';

import { TargetLocator } from 'selenium-webdriver';

import { HttpService } from 'src/app/shared/service/http.service';

import { AppAnchorDirective } from './../route/entry/anchor.directive';
import { AppGlobalService } from './../shared/service/app-global.service';

@Component({
  selector: 'app-search-history',
  templateUrl: './search-history.page.html',
  styleUrls: ['./search-history.page.scss'],
})
export class SearchHistoryPage implements OnInit {
  cityCode: number;      // 城市code
  keyword = '';   // 搜索关键词
  hasSearched: Boolean = false;     // 是否已经搜索
  searchArr: any[];       // 搜索历史记录数组
  tagData: any;
  searchType: any;
  searchBtnColor = '#858894';
  listId: any; // 搜索相应的列表用的id
  constructor(
    private route: ActivatedRoute,
    private appGlobalService: AppGlobalService,
    private http: HttpService,
  ) { }

  ngOnInit() {
    this.route.queryParams.subscribe(p => {
      console.log('search queryParams ---->', JSON.parse(p.obj));
      this.tagData = JSON.parse(p.obj);
      switch (+this.tagData.searchType) {
        case 1:
          this.searchType = 'all';
          break;
        case 2:
          this.searchType = 'transfer'; // 转店
          break;
        case 3:
          this.searchType = 'findShop';
          break;
        case 4:
          this.searchType = 'case'; // 案例
          break;
        case 5:
          this.searchType = 'counselor'; // 顾问
          break;

        default:
          this.searchType = 'all'; // 全部
          break;
      }
      console.log('search type ---->', this.searchType);
      this.searchArr = JSON.parse(localStorage.getItem(this.searchType))
        ? JSON.parse(localStorage.getItem(this.searchType)) : [];
    });
    //   const param = {
    //     pageId: this.tagData.link.target,
    //     params: {},
    //     sessionId: localStorage.getItem('sessionId')
    // };
    //   this.http.post('application/page', param, res => {
    //     const pageData = JSON.parse(res.script);
    //     this.listId = pageData.views[0].tags[0].data.key,
    //     console.log('this.listId ------> ' , this.listId);
    // });


  }

  focusInput() {
    this.hasSearched = false;
    this.keyword = '';
  }

  searchChange() {
   if (this.keyword === '') {
    this.searchBtnColor = '#858894';
   } else {
    this.searchBtnColor = '#27282E';
   }
  }
  search(str) {

    if (str === '' && this.keyword === '') {
      return ;
    }
    // this.hasSearched = true;
    const len = this.searchArr.length;
    if (!str) {
      if (this.keyword !== '' && this.searchArr.indexOf(this.keyword) === -1) {
        this.searchArr.unshift(this.keyword);
        if (len >= 10) {
          this.searchArr.pop();
        }
        localStorage.setItem(this.searchType, JSON.stringify(this.searchArr));
      }
    } else {
      this.keyword = str;
    }

    const session = localStorage.getItem('sessionId');

    // 参数组合
    for (const prop in this.tagData.link.params) {
      this.tagData.link.params[prop] = this.keyword;
    }
    this.keyword  = '';
    const pageParams = {
      sessionId: session,
      pageId: this.tagData.link.target,
      params: {
        search: this.tagData.link.params
      },
      
    };
    console.log('searchParams ----->\n', pageParams);
    if (localStorage.getItem('targetObj') === null) {
      localStorage.setItem('searchParams', JSON.stringify(pageParams));
      this.appGlobalService.goToForward(pageParams);
    } else {
      localStorage.setItem('searchParams', JSON.stringify(pageParams));
      window.history.back();
    }
  }



  clearHistory() {
    this.searchArr = [];
    localStorage.removeItem(this.searchType);
  }
  back() {
    window.history.back();
  }


}

